html, body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}

body {
  color: azure;
  background-color: #000;
  min-height: 100%;
  font-family: Helvetica, sans-serif;
  position: relative;
}

h1 {
  align-self: center;
  margin: .25em 0;
  padding: 0;
  font-size: 3em;
  display: inline-block;
}

button {
  cursor: pointer;
}

p {
  margin: .5em;
  padding: .5em;
}

.App {
  flex-direction: column;
  flex: 1;
  margin: 5px;
  display: flex;
}

.sideBar {
  flex: 1;
}

.winner {
  color: #f5f5f5;
  background-color: green;
  margin: .15em 0 0;
  padding: .5em;
  font-size: 3em;
}

.games {
  flex-direction: row;
  margin-top: 1em;
  display: flex;
}

.games button {
  flex: 1;
  padding: .5em;
  font-size: 1.25em;
}

.options, .options button {
  font-size: 1.5em;
}

.options {
  padding: .33em .5em;
}

.options input, .options select {
  margin: .25em 0 0;
  font-size: 1.25em;
}

.options input {
  text-align: center;
  width: 3.65em;
  margin-right: .25em;
}

input[type="number"]::-webkit-inner-spin-button {
  appearance: none;
}

input[type="number"]::-webkit-outer-spin-button {
  appearance: none;
}

.voice {
  max-width: 100%;
}

.playerChooser {
  flex-flow: wrap;
  row-gap: .35em;
  margin: 1em 0;
  padding: 0 .25em;
  font-size: 1.25em;
  display: flex;
}

.playerChooser button, .playerChooser input {
  font-size: 1em;
}

.playerChooser .player {
  border: 1px solid gray;
  flex: 0;
  align-items: center;
  margin-right: 1em;
  padding: .5em;
  display: flex;
}

.playerChooser .player button {
  margin: 0 .5em 0 0;
}

.addPlayer {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.addPlayer button {
  margin-top: .25em;
  padding: .25em;
  font-size: 1.25em;
}

.addPlayer input {
  font-size: 1.25em;
}

.dart {
  white-space: nowrap;
  border: 1px solid #000;
  margin: .25em;
  padding: .2em;
  font-family: Helvetica, sans-serif;
  font-weight: 500;
  display: inline-block;
}

.dart.condensed {
  border: none;
  padding: 0;
}

.players {
  text-align: center;
  flex-direction: row;
  margin-top: 5px;
  display: flex;
}

.player {
  white-space: nowrap;
  flex: 1;
  margin-bottom: .25em;
  padding-top: 2.7em;
}

.player.current {
  background-color: red;
  flex: 1.2;
  padding-top: 0;
  overflow-y: auto;
}

.player.current .name {
  color: #fff;
  font-size: 3em;
}

.player.current .total {
  color: #fff;
  font-size: 3.5em;
}

.dart.placeholder {
  border-color: #0000;
}

@media only screen and (width <= 600px) and (width >= 1px) {
  .player.current .name {
    font-size: 2.25em;
  }

  .player.current .total {
    font-size: 2.75em;
  }
}

.controls {
  justify-content: space-around;
  margin-top: .5em;
  display: flex;
}

.marks {
  flex-wrap: wrap;
  flex: 1;
  margin-top: .5em;
  display: flex;
}

.marks button {
  transition: background-color .5s ease-in;
}

.marks.invalid button, .controls.invalid button {
  background-color: red;
  transition: background-color .5s ease-out;
}

.marks button, .controls button {
  white-space: nowrap;
  cursor: pointer;
  -webkit-user-select: none;
  user-select: none;
  background-color: #f8f8ff;
  border: 1px solid #a9a9a9;
  flex: 1;
  min-width: 2.1em;
  margin: 0;
  padding: 0;
  font-size: 2.5em;
  font-weight: 600;
}

.controls button {
  aspect-ratio: 1;
  flex: none;
  justify-content: space-between;
  padding: 0 .5em;
  font-size: 1.5em;
}

.controls button:active {
  color: red;
}

.controls button.engaged {
  color: #fff;
  background: #000;
}

@media only screen and (width <= 600px) and (width >= 1px) {
  .marks button {
    font-size: 1.75em;
  }

  .controls button {
    font-size: 1em;
  }
}

@media (hover: hover) {
  .marks button:hover {
    color: #fff;
    background-color: #a9a9a9;
    border: 1px solid #a9a9a9;
  }
}

.gameInfo {
  align-content: center;
  align-items: stretch;
  margin: .25em .25em 0 0;
  padding: 0;
  font-size: 1.75em;
  display: flex;
}

.gameInfo > div {
  text-align: center;
  flex: 1;
  margin: 0;
}

@media only screen and (width <= 600px) and (width >= 1px) {
  .gameInfo {
    font-size: 1.25em;
  }
}

.stats {
  flex-wrap: wrap;
  display: flex;
}

.centered {
  text-align: center;
}

.playerStats {
  border: 2px solid #b8860b;
  flex: 0;
  margin: 3px;
  padding: .75em;
}

.playerStats.win {
  border-color: green;
}

.playerStats table {
  margin-top: 1em;
}

table .right {
  text-align: right;
  padding-right: .75em;
}

tr.total td {
  border-top: .5px solid #d3d3d3;
}

.backAgain {
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding: 1em;
  display: flex;
}

.backAgain button {
  background-color: #f8f8ff;
  flex: .25;
  padding: .5em;
  font-size: 2em;
}

.scoreBoard {
  flex-direction: row;
  flex: 2.5;
  display: flex;
}

@media only screen and (height <= 475px) and (height >= 1px) {
  .scoreBoard {
    flex-grow: 2;
  }
}

.column {
  border-right: 1px solid azure;
  flex-direction: column;
  flex: 1;
  font-size: .75em;
  display: flex;
  overflow: hidden;
}

.scoreBoard .score {
  font-family: Helvetica, sans-serif;
  font-size: 2.25em;
}

.scoreBoard .name {
  white-space: nowrap;
  overflow: scroll;
}

@media only screen and (width <= 600px) and (width >= 1px) {
  .score {
    font-size: 1.75em;
  }
}

.column.current {
  border: 1px solid red;
}

.column > div {
  text-align: center;
  flex: 1;
  justify-content: center;
  align-items: center;
  font-family: shadows into light regular, Helvetica, sans-serif;
  font-size: 2em;
  display: flex;
}

.column > div.x {
  padding: 0;
  font-size: 3.1em;
  line-height: .75em;
}

.column > div.slash {
  font-size: 2em;
}

.column:last-child {
  border: none;
}

.column .playerForRow {
  border-bottom: 1px solid azure;
  flex-direction: column;
  flex: none;
  display: flex;
}

.column .playerForRow.current {
  background-color: red;
}

.column .markLabel {
  background-color: #ffffff14;
  border: 1px solid #ffffff1a;
  border-radius: 5px;
  margin: 10px;
  font-family: Helvetica, sans-serif;
}

.cleared {
  color: gray;
  background-color: #000 !important;
  border: none !important;
}

.strikeThrough {
  background-color: #fff;
  width: 90vw;
  height: 2px;
  position: absolute;
}

.above {
  z-index: 11;
  position: relative;
}

.behind {
  z-index: 0;
  text-align: center;
  position: absolute;
  left: 0;
  right: 0;
}

.pips {
  flex-direction: row;
  display: flex;
}

.pips > div {
  border: 2px solid #fff;
  border-radius: 1em;
  width: 1em;
  height: 1em;
  margin-top: .2em;
}

.pips .filled {
  background-color: #fff;
}

#root {
  height: 100%;
}

.root {
  flex-direction: column;
  height: 100%;
  padding: 0 0 .25em;
  display: flex;
}
